.painting{
    box-shadow: 0 1rem 1rem rgba(0,0,0,0.2);
    position: relative;
}
 
.painting__link{
    width:100%;
    height:100%;
    display:block;
}
/* Overlay which is shown on Hover, needed z-index because the gradient of the CardOverlay needs to be on top! */
.painting::after{
    content:"";
    height:100%;
    width:100%;
    background: var(--color-white);
    position: absolute;
    /* Changes to 0.5 on hover */
    opacity:0;
    top:0;
    left:0;
    z-index: -1;
    transition: 0.2s opacity;
}

.painting:hover:after{
    opacity:0.5;
    cursor:pointer;
}

.painting__figure{
