.modal{
    background: rgba(0,0,0,0.75);
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    z-index: 2;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding:4rem;
}

.modal__buttonWrapper{
    width:100%;
    background:blue;
}

.modal__figure{
    position:relative;
}
.modal__image{
    width:100%;
    height:100%;
}

.modal__button{
    position:absolute;
    right:0;
    top:0;
    font-size: 1.4rem;
    line-height: 1.5rem;
    letter-spacing: 0.3rem;
    text-transform: uppercase;
    color: var(--color-white);
    background:none;
    border:none;
    font-weight:700;
    -webkit-transform: translateY(calc(-100% - 4rem));
            transform: translateY(calc(-100% - 4rem));
    transition: opacity 0.2s;
}

.modal__button:hover{
    cursor:pointer;
    opacity:0.25;
}
@supports ((-webkit-backdrop-filter: blur(0.5rem)) or (backdrop-filter: blur(0.5rem))){
    .modal{
        -webkit-backdrop-filter: blur(0.5rem);
                backdrop-filter: blur(0.5rem);
    }
}

@media screen and (max-width: 87.5rem) {
    .modal{
        justify-content: flex-start;
        padding-top: 10rem;
    }

    .modal__button{ 
        right: 2rem;
    }
}
.a-popup{
    -webkit-animation-name: popup;
            animation-name: popup;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
    -webkit-animation-fill-mode:backwards;
            animation-fill-mode:backwards;
}

@-webkit-keyframes popup {
    from{
        opacity:0;
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }
    to{
        opacity:1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes popup {
    from{
        opacity:0;
        -webkit-transform: translateY(2rem);
                transform: translateY(2rem);
    }
    to{
        opacity:1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

.a-show-modal{
    -webkit-animation-name: showModal;
            animation-name: showModal;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}

@-webkit-keyframes showModal {
    from{
        -webkit-filter: blur(0.5rem);
                filter: blur(0.5rem);
    }
    to{
        -webkit-filter: blur(0);
                filter: blur(0);
    }
}

@keyframes showModal {
    from{
        -webkit-filter: blur(0.5rem);
                filter: blur(0.5rem);
    }
    to{
        -webkit-filter: blur(0);
                filter: blur(0);
    }
}



.footer{
   width:100%;
   position: relative;
}

progress{
  -webkit-appearance: none;
  appearance: none;
}

  progress::-webkit-progress-bar {
    background: var(--color-header-border-progress-background);

  }
  progress::-webkit-progress-value {
    background:var(--font-color-black);

  }
  progress::-moz-progress-bar {
    background:var(--font-color-black);
  }
/* styling the background of the progressbar*/
.footer__progressBar{
    width:100%;
    height: 0.2rem;
    background: var(--color-header-border-progress-background);
    border:none;
}

.footer__wrapper{
    height:8rem; 
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4rem;
}

.footer__headers{
   display:flex;
   flex-direction: column;
}

.footer__headers > h5{
    margin-top: 1rem;
}

.footer__paintingHeader{
    font-family: 'Libre Baskerville', serif;
    color: var(--font-color-black);
    font-size: 1.8rem;
    line-height: 2.2rem;
    font-weight: 700;
}

.footer__artistHeader{
    font-family: 'Libre Baskerville', serif;
    font-size: 1.3rem;
    font-weight: 400;
    line-height: 1.7rem;
    opacity:0.75;
}

.footer__buttons{
    display:flex;
    flex-basis: 50%;
    justify-content: flex-end;
}

.footer__button:nth-of-type(1){
    margin-right: 4rem;
}

.footer__button{
    background: none;
    border:none;
    transition: 0.2s opacity;
}

.footer__button:hover{
    opacity:0.5;
    cursor:pointer;
}


/* footer disabled button modifiers*/
.footer__button--disabled{
    opacity: 0.15;
}

.footer__button--disabled:hover{
    opacity: 0.15;
    cursor: not-allowed;
}

