*, *::before, *::after{
    padding:0;
    margin:0;
    box-sizing: border-box;
}

*:focus{
    outline: 0.5rem dashed #000000;
    outline: 0.5rem dashed var(--font-color-black);
}

:root{
 /* Used colors */
 --font-color-black: #000000;
 --font-color-white: #FFFFFF;
 --font-link-color: #7D7D7D;
 --color-header-border-progress-background: #E5E5E5;
 --color-year-heading: rgb(224, 223, 223);
